<template>
  <div class="blog-post">
    <router-link to="/" class="back-link">‚Üê {{ $t('blog.back_to_list') }}</router-link>
    
    <div v-if="loading" class="loading">
      {{ $t('blog.loading') }}
    </div>
    
    <div v-else-if="error" class="error">
      {{ $t('blog.post_not_found') }}
    </div>
    
    <div v-else class="content" v-html="content"></div>
  </div>
</template>

<script>
import MarkdownIt from 'markdown-it'
import translations from '../translations.js'

export default {
  name: 'BlogPost',
  props: {
    currentLanguage: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      content: '',
      loading: true,
      error: false,
      md: new MarkdownIt(),
      blogNumber: null
    }
  },
  methods: {
    $t(key) {
      return translations[this.currentLanguage][key] || key
    },
    normalizeText(text) {
      return text
        .toLowerCase()
        .replace(/ƒü/g, 'g')
        .replace(/√º/g, 'u')
        .replace(/≈ü/g, 's')
        .replace(/ƒ±/g, 'i')
        .replace(/√∂/g, 'o')
        .replace(/√ß/g, 'c')
        .replace(/[^\w\s-]/g, '')
        .replace(/\s+/g, '-')
        .trim()
    },
    async calculateHash(text) {
      const msgBuffer = new TextEncoder().encode(text)
      const hashBuffer = await crypto.subtle.digest('SHA-256', msgBuffer)
      const hashArray = Array.from(new Uint8Array(hashBuffer))
      const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('')
      return hashHex.substring(0, 4)
    },
    async findBlogNumber() {
      // Extract hash from URL (e.g., "ration-calculation-a1b2" -> "a1b2")
      const urlParts = this.$route.params.title.split('-')
      const hash = urlParts[urlParts.length - 1]
      
      // Try all blog posts until we find a match
      for (let i = 1; i <= 7; i++) {
        const expectedHash = await this.calculateHash(`blog${i}`)
        if (expectedHash === hash) {
          this.blogNumber = i
          return i
        }
      }
      throw new Error('Blog post not found')
    },
    async loadContent() {
      this.loading = true
      this.error = false
      try {
        // If we don't have a blog number yet, find it
        if (!this.blogNumber) {
          await this.findBlogNumber()
        }
        
        // Use the stored blog number to load content
        const response = await fetch(`/markdowns/${this.currentLanguage}/blog${this.blogNumber}.md`)
        if (!response.ok) throw new Error('Blog post not found')
        const text = await response.text()
        this.content = this.md.render(text)
      } catch (err) {
        console.error('Error loading blog post:', err)
        this.error = true
      } finally {
        this.loading = false
      }
    }
  },
  watch: {
    '$route': 'loadContent',
    currentLanguage: 'loadContent'
  },
  mounted() {
    this.loadContent()
  }
}
</script>

<style scoped>
.blog-post {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.markdown-content {
  line-height: 1.6;
  font-size: 1.1rem;
}

.markdown-content h1,
.markdown-content h2,
.markdown-content h3 {
  margin-top: 2rem;
  margin-bottom: 1rem;
  color: #2c3e50;
}

.back-link {
  display: inline-block;
  margin-top: 2rem;
  padding: 8px 16px;
  background-color: #42b983;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.back-link:hover {
  background-color: #3aa876;
}

.loading {
  text-align: center;
  padding: 2rem;
  color: #666;
}

.error {
  text-align: center;
  padding: 2rem;
