<template>
  <div class="calculator">
    <div class="calculator-header">
      <h1>{{ $t('homepage.title') }}</h1>
      <p class="calculator-subtitle">{{ $t('page1.intro') }}</p>
    </div>

    <div class="calculator-container">
      <form @submit.prevent="handleSubmit" class="calculator-form">
        <div class="form-grid">
          <div class="form-group">
            <div class="label-container">
              <label for="lactationNumber">{{ $t('homepage.lactation') }}</label>
              <div class="help-container">
                <button 
                  type="button" 
                  class="help-button" 
                  @click="showHelp('lactation')"
                  @mouseenter="showTooltip('lactation')"
                  @mouseleave="hideTooltip"
                >?</button>
                <div v-if="activeTooltip === 'lactation'" class="tooltip">
                  {{ $t('help.lactation') }}
                </div>
              </div>
            </div>
            <input
              type="number"
              id="lactationNumber"
              v-model.number="formData.lactationNumber"
              min="1"
              step="1"
              required
              class="form-input"
            >
          </div>

          <div class="form-group">
            <div class="label-container">
              <label for="dailyMilkYield">{{ $t('homepage.daily') }}</label>
              <div class="help-container">
                <button 
                  type="button" 
                  class="help-button" 
                  @click="showHelp('daily')"
                  @mouseenter="showTooltip('daily')"
                  @mouseleave="hideTooltip"
                >?</button>
                <div v-if="activeTooltip === 'daily'" class="tooltip">
                  {{ $t('help.daily') }}
                </div>
              </div>
            </div>
            <input
              type="number"
              id="dailyMilkYield"
              v-model.number="formData.dailyMilkYield"
              min="0"
              step="0.1"
              required
              class="form-input"
            >
          </div>

          <div class="form-group">
            <div class="label-container">
              <label for="milkFatPercentage">{{ $t('homepage.fat') }}</label>
              <div class="help-container">
                <button 
                  type="button" 
                  class="help-button" 
                  @click="showHelp('fat')"
                  @mouseenter="showTooltip('fat')"
                  @mouseleave="hideTooltip"
                >?</button>
                <div v-if="activeTooltip === 'fat'" class="tooltip">
                  {{ $t('help.fat') }}
                </div>
              </div>
            </div>
            <input
              type="number"
              id="milkFatPercentage"
              v-model.number="formData.milkFatPercentage"
              min="0"
              max="100"
              step="0.1"
              required
              class="form-input"
            >
          </div>

          <div class="form-group">
            <div class="label-container">
              <label for="weekOfLactation">{{ $t('homepage.week') }}</label>
              <div class="help-container">
                <button 
                  type="button" 
                  class="help-button" 
                  @click="showHelp('week')"
                  @mouseenter="showTooltip('week')"
                  @mouseleave="hideTooltip"
                >?</button>
                <div v-if="activeTooltip === 'week'" class="tooltip">
                  {{ $t('help.week') }}
                </div>
              </div>
            </div>
            <input
              type="number"
              id="weekOfLactation"
              v-model.number="formData.weekOfLactation"
              min="1"
              step="1"
              required
              class="form-input"
            >
          </div>

          <div class="form-group">
            <div class="label-container">
              <label for="liveWeight">{{ $t('homepage.weight') }}</label>
              <div class="help-container">
                <button 
                  type="button" 
                  class="help-button" 
                  @click="showHelp('weight')"
                  @mouseenter="showTooltip('weight')"
                  @mouseleave="hideTooltip"
                >?</button>
                <div v-if="activeTooltip === 'weight'" class="tooltip">
                  {{ $t('help.weight') }}
                </div>
              </div>
            </div>
            <input
              type="number"
              id="liveWeight"
              v-model.number="formData.liveWeight"
              min="0"
              step="0.1"
              required
              class="form-input"
            >
          </div>

          <div class="form-group">
            <div class="label-container">
              <label for="gestationPeriod">{{ $t('homepage.gestation') }}</label>
              <div class="help-container">
                <button 
                  type="button" 
                  class="help-button" 
                  @click="showHelp('gestation')"
                  @mouseenter="showTooltip('gestation')"
                  @mouseleave="hideTooltip"
                >?</button>
                <div v-if="activeTooltip === 'gestation'" class="tooltip">
                  {{ $t('help.gestation') }}
                </div>
              </div>
            </div>
            <input
              type="number"
              id="gestationPeriod"
              v-model.number="formData.gestationPeriod"
              min="0"
              max="365"
              step="1"
              required
              class="form-input"
            >
          </div>
        </div>

        <div class="form-actions">
          <button type="submit" class="submit-button">
            <span>{{ $t('button.next') }}</span>
            <span class="arrow">‚Üí</span>
          </button>
        </div>
      </form>
    </div>

    <!-- Help Modal (Mobile Only) -->
    <div v-if="showModal" class="modal-overlay" @click="closeModal">
      <div class="modal-content" @click.stop>
        <div class="modal-body">
          {{ $t(`help.${activeHelp}`) }}
        </div>
        <button class="close-button" @click="closeModal">√ó</button>
      </div>
    </div>
  </div>
</template>

<script>
import RationCalculator from '../RationCalculator.js'
import translations from '../translations.js'

export default {
  name: 'Calculator',
  props: {
    currentLanguage: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      formData: {
        lactationNumber: 3,
        dailyMilkYield: 15,
        milkFatPercentage: 3.6,
        weekOfLactation: 30,
        liveWeight: 500,
        gestationPeriod: 80
      },
      showModal: false,
      activeHelp: null,
      activeTooltip: null
    }
  },
  methods: {
    $t(key) {
      return translations[this.currentLanguage][key] || key
    },
    showHelp(helpId) {
      if (window.innerWidth <= 768) {
        this.activeHelp = helpId
        this.showModal = true
      }
    },
    showTooltip(helpId) {
      if (window.innerWidth > 768) {
        this.activeTooltip = helpId
      }
    },
    hideTooltip() {
      this.activeTooltip = null
    },
    closeModal() {
      this.showModal = false
      this.activeHelp = null
    },
    handleSubmit() {
      const calculator = new RationCalculator(
        this.formData.lactationNumber,
        this.formData.dailyMilkYield,
        this.formData.milkFatPercentage,
        this.formData.weekOfLactation,
        this.formData.liveWeight,
        this.formData.gestationPeriod
      )
      const result = calculator.calculateRation()
      this.$router.push({
        name: 'FeedSelector',
        params: { calculatorResult: result }
      })
    }
  }
}
</script>

