<!-- src/components/Help.vue -->
<template>
  <div class="help">
    <div v-if="loading" class="loading">
      {{ $t('help.loading') }}
    </div>
    
    <div v-else-if="error" class="error">
      {{ $t('help.error') }}
    </div>
    
    <div v-else class="content" v-html="content"></div>
  </div>
</template>

<script>
import MarkdownIt from 'markdown-it'
import translations from '../translations.js'

export default {
  name: 'Help',
  props: {
    currentLanguage: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      content: '',
      loading: true,
      error: false,
      md: new MarkdownIt()
    }
  },
  methods: {
    $t(key) {
      return translations[this.currentLanguage][key] || key
    },
    async loadContent() {
      this.loading = true
      this.error = false
      try {
        const response = await fetch(`/markdowns/${this.currentLanguage}/help.md`)
        if (!response.ok) throw new Error('Help content not found')
        const text = await response.text()
        this.content = this.md.render(text)
      } catch (err) {
        console.error('Error loading help content:', err)
        this.error = true
      } finally {
        this.loading = false
      }
    }
  },
  watch: {
    currentLanguage: 'loadContent'
  },
  mounted() {
    this.loadContent()
  }
}
</script>

<style scoped>
.help {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.content {
  line-height: 1.6;
  font-size: 1.1rem;
}

.content h1,
.content h2,
.content h3 {
  margin-top: 2rem;
  margin-bottom: 1rem;
  color: #2c3e50;
}

.loading {
  text-align: center;
  padding: 2rem;
  color: #666;
}

.error {
  text-align: center;
  padding: 2rem;
  color: #e74c3c;
}

.error p {
  margin-bottom: 1rem;
}
</style> 